import {
  Route,
  Redirect
} from 'react-router-dom';

const ToHome = () => <Redirect to ="/" />;

const Redirector = () => <>
  <Route exact path="/store-macias-nicolas">
   <ToHome />
  </Route>
  <Route exact path="/**">
    <ToHome />
  </Route>
</>

export default Redirector;
